import { React, useState } from "react";
import * as s from "../styles/globalStyles";
import "./Navigation.css";
import "react-js-stickynav/dist/index.css";
import discord_icon from "../_resources/icons/discord.svg";

import ReactDOM from "react-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faDiscord, faTwitter } from "@fortawesome/free-brands-svg-icons";

function StickyNavBar() {
  const [selectedId, setSelectedId] = useState(0);

  const pages = {
    "#home": 0,
    "#mint": 1,
    "#roadmap": 2,
    "#team": 3,
    "#faq": 4,
    "": -1,
  };

  const ButtonHandler = (e) => {
    setSelectedId(e.target.id);
  };

  const GetCurrentURLHash = () => {
    let currentHash = window.location.hash;
    console.log("Hash: ", currentHash);
    // console.log("ID: ", pages[currentHash]);

    console.log(selectedId);
    console.log("New Hash:", pages[currentHash]);
    setSelectedId(pages[currentHash]);
  };

  window.addEventListener("hashchange", function () {
    console.log("location changed!");
    GetCurrentURLHash();
  });

  // https://reactnavigation.org/docs/configuring-links/

  return (
    <s.NavigationBar>
      {/* Add viewport logic */}
      <s.NavigationButton
        // className={"" + (selectedId === "-1" ? " active_button" : "no")}
        // id="-1"
        href="#home"
        onClick={(e) => {
          // e.preventDefault();
          ButtonHandler(e);
        }}
      >
        Home
      </s.NavigationButton>
      <s.SpacerMax></s.SpacerMax>
      <s.NavigationButton
        className={"" + (selectedId == "0" ? " active_button" : "no")}
        id="0"
        href="#home"
        onClick={(e) => {
          // e.preventDefault();
          ButtonHandler(e);
        }}
      >
        Home
      </s.NavigationButton>

      <s.NavigationButton
        className={"" + (selectedId == "1" ? " active_button" : "no")}
        id="1"
        href="#mint"
        onClick={(e) => {
          // e.preventDefault();
          ButtonHandler(e);
        }}
      >
        Mint
      </s.NavigationButton>

      <s.NavigationButton
        className={"" + (selectedId == "2" ? " active_button" : "no")}
        id="2"
        href="#roadmap"
        onClick={(e) => {
          // e.preventDefault();
          ButtonHandler(e);
        }}
      >
        Roadmap
      </s.NavigationButton>

      <s.NavigationButton
        className={"" + (selectedId == "3" ? " active_button" : "no")}
        id="3"
        href="#team"
        onClick={(e) => {
          // e.preventDefault();
          ButtonHandler(e);
        }}
      >
        Team
      </s.NavigationButton>

      <s.NavigationButton
        className={"" + (selectedId == "4" ? " active_button" : "no")}
        id="4"
        href="#faq"
        onClick={(e) => {
          // e.preventDefault();
          ButtonHandler(e);
        }}
      >
        {/* <FontAwesomeIcon icon="fa-brands fa-discord" /> */}
        FAQ
      </s.NavigationButton>

      <s.NavigationButton href="https://discord.gg/3urXQJX4Ru" target="blank">
        <FontAwesomeIcon icon={faDiscord} />
      </s.NavigationButton>

      <s.NavigationButton
        href="https://twitter.com/AlphaDracoNFT"
        target="blank"
      >
        <FontAwesomeIcon icon={faTwitter} />
      </s.NavigationButton>
    </s.NavigationBar>
  );
}

export { StickyNavBar };
