import * as React from "react";
import * as s from "../styles/globalStyles";

function Countdown() {
  // Get time now
  const TIME_NOW = Date.now();
  //   console.log(
  //     "TIME_NOW: ",
  //     TIME_NOW / 1000,
  //     "   ",
  //     Math.round(TIME_NOW / 1000)
  //   );

  const END_TIME = 1643644800;

  let timeLeft = Math.round(END_TIME - TIME_NOW / 1000);
  // convert to ms - e.g. 1641252957

  // negate by end time - - e.g. 1643931304 (feb)
  // update counter as difference: 2,678,347

  //   https://www.epochconverter.com/
  // Tuesday, February 1, 2022 12:00:00 AM GMT+08:00

  const [counter, setCounter] = React.useState(timeLeft);

  let days = Math.floor(counter / 86400);
  let hours = Math.floor((counter % 86400) / 3600);
  let minutes = Math.floor((counter % 3600) / 60);
  let seconds = Math.floor(counter % 60);
  let append_seconds = "0" + seconds;
  let append_minutes = "0" + minutes;
  let append_hours = "0" + hours;
  let append_days = "0" + days;
  // Suggested by Laurent
  React.useEffect(() => {
    counter > 0 && setTimeout(() => setCounter(counter - 1), 1000);
  }, [counter]);

  return (
    <s.TempContainer>
      <s.TextTitle>
        {days < 10 ? append_days : days}:
        {hours < 10 ? append_hours : hours}:
        {minutes < 10 ? append_minutes : minutes}:
        {seconds < 10 ? append_seconds : seconds}
      </s.TextTitle>
      <s.TextDescription>Look out for our Minting Launch!</s.TextDescription>
      <s.TextDescription>
        TBC: February 1, 2022 12:00:00 AM GMT+08:00
      </s.TextDescription>
    </s.TempContainer>
  );
}

export { Countdown };
