import React, { useEffect, useState, useRef } from "react";
import "../App.css";
import { useDispatch, useSelector } from "react-redux";
// import { connect, fetchData } from "react-redux";
import { connect } from "../redux/blockchain/blockchainActions";
import { fetchData } from "../redux/data/dataActions";
import * as s from "../styles/globalStyles";
import styled from "styled-components";

export const StyledButton = styled.button`
  padding: 8px;
  
`;

function MintComponent() {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  const data = useSelector((state) => state.data);

  const [claimingNft, setClaimingNft] = useState(false);
  const [feedback, setFeedback] = useState("Maybe it's your lucky day!");

  const claimNFTs = (_amount) => {
    setClaimingNft(true);
    const amount = _amount || 1;
    blockchain.smartContract.methods
      .mint(blockchain.account, _amount)
      .send({
        from: blockchain.account,
        // TODO: UPDATE THIS MINT COST ACCORDINGLY
        value: blockchain.web3.utils.toWei(String(0.05 * _amount), "ether"),
      })
      .once("error", (error) => {
        console.log(error);
        setFeedback("Error");
        setClaimingNft(false);
      })
      .then((receipt) => {
        setFeedback(
          "Claim successfully! Go to https://testnets.opensea.io/ to see it."
        );
        setClaimingNft(false);
      });
  };

  useEffect(() => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
    }
  }, [blockchain.smartContract, dispatch]);

  return (
    <s.Container>
      <s.StickyBarSpacer />
      {blockchain.account === "" || blockchain.smartContract === null ? (
        <s.Container flex={1} ai={"center"} jc={"center"}>
          <s.TextTitle>Connect to the Rinkeby Test Net</s.TextTitle>
          <s.SpacerSmall />
          <StyledButton
            onClick={(e) => {
              e.preventDefault();
              dispatch(connect());
            }}
          >
            CONNECT
          </StyledButton>
          <s.SpacerSmall />
          {blockchain.errorMsg !== "" ? (
            <s.TextDescription>{blockchain.errorMsg}</s.TextDescription>
          ) : null}
        </s.Container>
      ) : (
        <s.Container flex={1} ai={"center"} style={{ padding: 24 }}>
          <s.TextTitle style={{ textAlign: "center" }}>
            NFT Collection <br></br> [ {data.name} ]
          </s.TextTitle>
          <s.SpacerSmall />
          <StyledButton
            disabled={claimingNft ? 1 : 0}
            onClick={(e) => {
              e.preventDefault();
              claimNFTs(1);
            }}
          >
            {claimingNft ? "Claiming..." : "Mint 1 NFT for 0.05 ETH"}
          </StyledButton>
          <s.SpacerSmall />
          <StyledButton
            disabled={claimingNft ? 1 : 0}
            onClick={(e) => {
              e.preventDefault();
              claimNFTs(5);
            }}
          >
            {claimingNft ? "Claiming..." : "Mint 5 NFT for 0.25 ETH"}
          </StyledButton>
          <s.SpacerSmall />
          <s.TextDescription style={{ textAlign: "center" }}>
            {feedback}
          </s.TextDescription>
          <s.SpacerMedium />
        </s.Container>

        //  Get rinkeby ETH here: https://faucets.chain.link/rinkeby
        // Check your collection on Opensea testnet: https://testnets.opensea.io/0x06afacd25eff7940e116513e833c58000c765bd5
        //  Replace with your wallet address hash
      )}
    </s.Container>
  );
}

export {MintComponent};
